@import url(https://fonts.googleapis.com/css2?family=Nova+Slim&family=Rhodium+Libre&family=Single+Day&display=swap);
* {
  margin:0;
}
.app{
  /* background-image: url('https://i.ibb.co/JdMhPhz/Fondo-SVG7.png'); */
  height: 100vh;
}

.app__header{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  object-fit: contain;
  
}

.app__headerImage{
  padding: 20px;
  object-fit: contain;
}
.app__loginContainer{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.app__signUp{
  display: flex;
  flex-direction: column;
}

.postContainer{
  background-color: #c9767649;

}

.posteo{
  background-color: black;
}
.post{
    background-color:#00000049;
    max-width: 520px;
    display:flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: column;
    margin-bottom: 45px;
    border-radius: 12px;
}

.post__image{
    display:flex;
    flex-wrap: wrap;
    flex-direction: column;
    width: 80%;
    object-fit: contain;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
}

.post__text{
    font-weight: normal;
    padding: 20px;
}

.post__header{
    display: flex;
    align-items: center;
    padding: 20px;
}

.post__avatar{
    margin-right: 10px;
}
.contenedor{
    display: flex;
    width: 800px;
    height: 60px;
    font-size: 60px;
    color: #1c5c40;
    line-height: 59.5px;
    overflow: hidden;
    font-family: 'Rhodium Libre', serif;
}

ul{
    list-style: none;
    padding-left: 10px;
    -webkit-animation: cambiar 7s infinite;
            animation: cambiar 7s infinite;

}

ul, p{
    margin:0;
}

pre {
    color: #ff0000;
    font-size: 60px;
    font-family: 'Rhodium Libre', serif;


 }

@-webkit-keyframes cambiar {
    
    0%{ margin-top: 0;}
    20%{ margin-top: 0;}

    25%{ margin-top: -60px;}
    50%{ margin-top: -60px;}

  



}

@keyframes cambiar {
    
    0%{ margin-top: 0;}
    20%{ margin-top: 0;}

    25%{ margin-top: -60px;}
    50%{ margin-top: -60px;}

  



}


