.post{
    background-color:#00000049;
    max-width: 520px;
    display:flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: column;
    margin-bottom: 45px;
    border-radius: 12px;
}

.post__image{
    display:flex;
    flex-wrap: wrap;
    flex-direction: column;
    width: 80%;
    object-fit: contain;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
}

.post__text{
    font-weight: normal;
    padding: 20px;
}

.post__header{
    display: flex;
    align-items: center;
    padding: 20px;
}

.post__avatar{
    margin-right: 10px;
}