@import url('https://fonts.googleapis.com/css2?family=Nova+Slim&family=Rhodium+Libre&family=Single+Day&display=swap');
.contenedor{
    display: flex;
    width: 800px;
    height: 60px;
    font-size: 60px;
    color: #1c5c40;
    line-height: 59.5px;
    overflow: hidden;
    font-family: 'Rhodium Libre', serif;
}

ul{
    list-style: none;
    padding-left: 10px;
    animation: cambiar 7s infinite;

}

ul, p{
    margin:0;
}

pre {
    color: #ff0000;
    font-size: 60px;
    font-family: 'Rhodium Libre', serif;


 }

@keyframes cambiar {
    
    0%{ margin-top: 0;}
    20%{ margin-top: 0;}

    25%{ margin-top: -60px;}
    50%{ margin-top: -60px;}

  



}

